.btn
    display: inline-block
    padding: 12px 40px
    border: none
    font-weight: 500
    font-size: 16px
    line-height: 180%
    cursor: pointer
    +transitionDefault
    text-transform: uppercase
    text-decoration: none
    text-align: center
    letter-spacing: normal
    outline: none
    &:hover 
        text-decoration: none
    &:focus
        outline: 1px solid $activeColor

    svg 
        position: relative
        width: 12px
        height: 12px
        margin-left: 4px
        top: 0
        transition: 0.3s ease
        
    

    &--active
        background-color: $activeColor
        color: #fff

        svg 
            fill: #fff 

        &:hover
            outline: 1px solid $activeColor
            background-color: #F7F3EA
            color: $activeColor

            svg 
                fill: $activeColor

        &:active 
            outline: 2px solid $activeColor

    &--light 
        background: #F7F3EA
        color: $activeColor
        svg 
            fill: $activeColor

        &:hover
            outline: 1px solid $activeColor 

        &:active 
            outline: 2px solid $activeColor

    


